// Canvas

.c-canvas {
	width: 100%;
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 1;
	background-color: $color-black;

	svg {
		width: 100%;
		height: 100%;
		display: block;
	}
}



